.show {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.full-v {
    width: 100vw;
    height: 100vh;
}

.full-vw {
    width: 100vw;
}

.full-vh {
    height: 100vh;
}

.full {
    width: 100%;
    height: 100%;
}

.full-w {
    width: 100%;
}

.full-h {
    height: 100%;
}

/*\
 *  Template-1  -2017/01/14
 *  @mixin flexbox
 *  @mixin center
