.word {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 8rem;
    margin-bottom: 10px;
    font-size: 3rem;
    font-family: sans-serif;
}

.title {
    margin-left: 2rem;
}
.title small {
    font-family: monospace;
    font-size: 1rem;
    color: #2a74f6;
}

.block {
    width: 7rem;
    height: 7rem;
    margin: 2px;
    font-size: 1.5rem;
    background-color: #eee;
}

.blue {
    background-color: #2a74f6;
    color: #fff;
}

